import { useState } from "react"
import projectAbraThumbnail from "../assets/img/project_abrapalabra_500x500.webp"
import projectShredThumbnail from "../assets/img/project_shredsquad_500x500.webp"
import wwwIcon from "../assets/icons/internet-icon.svg"
import githubIcon from "../assets/icons/github-icon.svg"
import videoIcon from "../assets/icons/video-icon.svg"

const Projects = (props) => {
  props.loadTrigger()

  const projects = [
    {
      name: "AbraPalabra",
      devTools: ["html", "css", "js"],
      description:
        "Responsive Wordle clone with Spanish dictionary. Features both interactive Mouse/Touch keyboard & Typing input which changes dynamically depending on guessed letters.",
      thumbnail: projectAbraThumbnail,
      img: projectAbraThumbnail,
      siteLink: "https://abrapalabra.netlify.app/",
      githubLink: "https://github.com/prb01/abrapalabra",
      demoLink: "https://vimeo.com/707821696",
    },
    {
      name: "ShredSquad",
      devTools: ["html", "css", "js", "php", "sh"],
      description:
        "Implemented dark mode for wakeboarding team website. Dark mode applies if OS/Browser settings dark-mode applied, if user selects dark mode on site, otherwise default dark mode is driven by Sunrise / Sunset by querying weather API daily",
      thumbnail: projectShredThumbnail,
      img: projectShredThumbnail,
      siteLink: "https://shredsquad.herokuapp.com/",
      githubLink: "https://github.com/prb01/shredsquad",
      demoLink: "https://vimeo.com/707819729",
    },
    {
      name: "Project3",
      devTools: ["html", "css", "js"],
      description: "bla bla bla",
      thumbnail: "",
      img: "",
    },
    {
      name: "Project4",
      devTools: ["html", "css", "js"],
      description: "bla bla bla",
      thumbnail: "",
      img: "",
    },
    {
      name: "Project5",
      devTools: ["html", "css", "js"],
      description: "bla bla bla",
      thumbnail: "",
      img: "",
    },
    {
      name: "Project6",
      devTools: ["html", "css", "js"],
      description: "bla bla bla",
      thumbnail: "",
      img: "",
    },
  ]

  const Project = ({ project }) => {
    const [overlay, setOverlay] = useState(false)
    const [hidden, setHidden] = useState(true)

    const handleClick = (e) => {
      setOverlay(!overlay)
      overlay ? setTimeout(() => true, 500) : setHidden(false)

      const targets = document.querySelectorAll(".project")
      targets.forEach((target) => {
        if (!overlay) {
          target.classList.add("pointer-events-none", "user-select-none")
        } else {
          target.classList.remove(
            "pointer-events-none",
            "user-select-none"
          )
        }
      })
    }

    return (
      <div
        key={project.name}
        className="aspect-square h-32 xl:h-48 load zoomIn"
      >
        <div
          className={`
          project relative w-full h-full flex justify-center items-center hover:scale-105 bg-contain transition-all cursor-pointer`}
          style={{ backgroundImage: `url(${project.thumbnail})` }}
          onClick={handleClick}
        >
          <div className="absolute bg-accent/20 z-10 w-full h-full hover:bg-transparent"></div>
          <div className="text-offWhite font-bold text-md xl:text-lg bg-main/90 px-1 z-20">
            {project.name}
          </div>
        </div>
        <div
          id="project-overlay"
          className={`
          ${hidden ? "hidden" : ""}
          ${overlay ? "megaZoomIn" : "megaZoomOut"}
          fixed top-0 left-0 bottom-0 right-0 overflow-hidden z-30 transition-all origin-center flex justify-center items-center px-3`}
        >
          <div
            className="absolute top-0 bottom-0 right-0 left-0 bg-main/50"
            onClick={handleClick}
          ></div>
          <div
            className="absolute top-5 right-5 flex justify-center items-center text-offWhite cursor-pointer font-bold text-3xl"
            onClick={handleClick}
          >
            X
          </div>
          <div className="bg-offWhite rounded-lg p-3 xl:p-6 z-40">
            <div className="grid grid-cols-1 xl:grid-cols-2 gap-8 xl:gap-3 items-center justify-items-center">
              <div className="relative shadow-main shadow-xl w-40 xl:w-auto">
                <img src={project.img} />
                <div className="flex basis-1 absolute -bottom-3 left-1/2 -translate-x-1/2">
                  {project.devTools.map((tool) => (
                    <div
                      key={tool}
                      className="rounded-l-full rounded-r-full bg-accent px-3 mr-1"
                    >
                      {tool}
                    </div>
                  ))}
                </div>
              </div>
              <div className="grid-rows-3">
                <h2 className="font-bold mb-3 text-3xl text-center xl:mb-5">
                  {project.name}
                </h2>

                <div className="mb-5">{project.description}</div>
                <div className="flex basis-1 gap-6 justify-center">
                  <a href={project.siteLink} target="_blank">
                    <img
                      className="w-8 hover:scale-110 iconAccent transition-all"
                      src={wwwIcon}
                    />
                  </a>
                  <a href={project.githubLink} target="_blank">
                    <img
                      className="w-8 hover:scale-110 iconAccent transition-all"
                      src={githubIcon}
                    />
                  </a>
                  <a href={project.demoLink} target="_blank">
                    <img
                      className="w-8 hover:scale-110 iconAccent transition-all"
                      src={videoIcon}
                    />
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="grid grid-cols-2 xl:grid-cols-3 p-5 gap-2 items-center justify-items-center h-max">
      {projects.map((project) => (
        <Project key={project.name} project={project} />
      ))}
    </div>
  )
}

export default Projects
